  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.type !== 'all') {
      filtered = filtered.filter(item => item.type === this.filters.type);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.name && item.name.toLowerCase().includes(search)) ||
          (item.location && item.location.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [facilities, services, bookings, equipment, maintenance, reservations, analytics] =
        await Promise.all([
          this.apiClient.get(
            this.API_ENDPOINTS.facilities || '/api/advanced-facilities/facilities'
          ),
          this.apiClient.get(this.API_ENDPOINTS.services || '/api/advanced-facilities/services'),
          this.apiClient.get(this.API_ENDPOINTS.bookings || '/api/advanced-facilities/bookings'),
          this.apiClient.get(this.API_ENDPOINTS.equipment || '/api/advanced-facilities/equipment'),
          this.apiClient.get(
            this.API_ENDPOINTS.maintenance || '/api/advanced-facilities/maintenance'
          ),
          this.apiClient.get(
            this.API_ENDPOINTS.reservations || '/api/advanced-facilities/reservations'
          ),
          this.apiClient.get(this.API_ENDPOINTS.analytics || '/api/advanced-facilities/analytics'),
        ]);

      this.facilities = facilities.data || [];
      this.services = services.data || [];
      this.bookings = bookings.data || [];
      this.equipment = equipment.data || [];
      this.maintenance = maintenance.data || [];
      this.reservations = reservations.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading facilities data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-facilities', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.filters.search = event.target.value;
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('facilitiesContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

  getStatusText(status) {
    const statusMap = {
      available: 'ŸÖÿ™ÿßÿ≠',
      occupied: 'ŸÖÿ¥ÿ∫ŸàŸÑ',
      maintenance: 'ÿµŸäÿßŸÜÿ©',
      reserved: 'ŸÖÿ≠ÿ¨Ÿàÿ≤',
      active: 'ŸÜÿ¥ÿ∑',
      inactive: 'ÿ∫Ÿäÿ± ŸÜÿ¥ÿ∑',
      pending: 'ŸÇŸäÿØ ÿßŸÑÿßŸÜÿ™ÿ∏ÿßÿ±',
      confirmed: 'ŸÖÿ§ŸÉÿØ',
      cancelled: 'ŸÖŸÑÿ∫Ÿâ',
      completed: 'ŸÖŸÉÿ™ŸÖŸÑ',
    };
    return statusMap[status] || status;
  }

  getFacilityTypeText(type) {
    const typeMap = {
      room: 'ÿ∫ÿ±ŸÅÿ©',
      hall: 'ŸÇÿßÿπÿ©',
      gym: 'ÿµÿßŸÑÿ© ÿ±Ÿäÿßÿ∂Ÿäÿ©',
      pool: 'ŸÖÿ≥ÿ®ÿ≠',
      library: 'ŸÖŸÉÿ™ÿ®ÿ©',
    };
    return typeMap[type] || type;
  }

  formatCurrency(amount) {
    return new Intl.NumberFormat('ar-SA', {
      style: 'currency',
      currency: 'SAR',
      minimumFractionDigits: 2,
    }).format(amount);
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  formatDateTime(dateTime) {
    if (!dateTime) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(dateTime).toLocaleString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedFacilities', JSON.stringify(this.facilities));
      localStorage.setItem('advancedServices', JSON.stringify(this.services));
      localStorage.setItem('advancedBookings', JSON.stringify(this.bookings));
      localStorage.setItem('advancedEquipment', JSON.stringify(this.equipment));
      localStorage.setItem('advancedMaintenance', JSON.stringify(this.maintenance));
      localStorage.setItem('advancedReservations', JSON.stringify(this.reservations));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.facilities = JSON.parse(localStorage.getItem('advancedFacilities') || '[]');
      this.services = JSON.parse(localStorage.getItem('advancedServices') || '[]');
      this.bookings = JSON.parse(localStorage.getItem('advancedBookings') || '[]');
      this.equipment = JSON.parse(localStorage.getItem('advancedEquipment') || '[]');
      this.maintenance = JSON.parse(localStorage.getItem('advancedMaintenance') || '[]');
      this.reservations = JSON.parse(localStorage.getItem('advancedReservations') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createFacility = this.createFacility.bind(this);
    this.createService = this.createService.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewFacility = this.viewFacility.bind(this);
    this.editFacility = this.editFacility.bind(this);
  }

